import React, { useState } from 'react';
import Stage from './Stage';
import AddStageModal from './AddStageModal';
import PersonaLegend from './PersonaLegend';
import AggregatedPainpointView from './AggregatedPainpointView';
import SpreadsheetImportExportModal from './SpreadsheetImportExportModal';
import ZoomPanControls from './ZoomPanControls';
import useZoomPan from '../hooks/useZoomPan';
import { PERSONAS } from '../constants/personas';
import { getTerminology } from '../constants/mapTypes';

const JourneyMap = ({ 
  stages,
  journeyMapName,
  journeyMapType,
  currentView,
  onAddStage, 
  onUpdateStage,
  onDeleteStage,
  onAddTask,
  onUpdateTask,
  onDeleteTask,
  onAddStep, 
  onUpdateStep, 
  onDeleteStep,
  onSwitchToStepView,
  onImportData,
  onOpenStepDetail,
  onOpenEditPanel,
  editPanel,
  editablePainPoints,
  editableOpportunities,
  onCloseEditPanel,
  onAddPainPoint,
  onRemovePainPoint,
  onUpdatePainPoint,
  onAddOpportunity,
  onRemoveOpportunity,
  onUpdateOpportunity,
  onSaveEditChanges
}) => {
  const [isAddStageModalOpen, setIsAddStageModalOpen] = useState(false);
  const [isImportExportModalOpen, setIsImportExportModalOpen] = useState(false);

  // Initialize zoom and pan functionality
  const {
    zoom,
    pan,
    isDragging,
    zoomIn,
    zoomOut,
    resetZoom,
    fitToView,
    containerProps,
    canZoomIn,
    canZoomOut,
  } = useZoomPan();

  // Get terminology based on map type
  const terminology = getTerminology(journeyMapType);

  return (
    <div className="bg-white rounded-lg p-6 relative">
      <div className="flex items-center justify-between mb-6">
        <div className="flex items-center gap-6">
          <h2 className="text-xl font-semibold text-gray-800">{terminology.stages}</h2>
          <PersonaLegend personas={PERSONAS} />
        </div>
        <div className="flex items-center gap-3">
          {currentView === 'painpoint' && (
            <button
              onClick={() => setIsImportExportModalOpen(true)}
              className="bg-green-600 hover:bg-green-700 text-white px-4 py-2 rounded-lg transition-colors flex items-center gap-2"
            >
              <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 10v6m0 0l-3-3m3 3l3-3m2 8H7a2 2 0 01-2-2V5a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z" />
              </svg>
              Import/Export
            </button>
          )}
          <button
            onClick={() => setIsAddStageModalOpen(true)}
            className="bg-blue-600 hover:bg-blue-700 text-white px-4 py-2 rounded-lg transition-colors flex items-center gap-2"
          >
            <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 4v16m8-8H4" />
            </svg>
            Add {terminology.stage}
          </button>
        </div>
      </div>

      {/* Zoomable and Pannable Content Area */}
      <div className="relative bg-white rounded-lg border border-gray-200 min-h-96" style={{ height: 'calc(100vh - 300px)', overflow: 'hidden' }}>
        <div {...containerProps} className="bg-white" style={{ ...containerProps.style, minWidth: '100vw', minHeight: '100vh' }}>
          {currentView === 'step' ? (
            <div className="flex gap-6 p-6 min-w-max bg-white min-h-full">
              {stages.map((stage) => (
                <Stage
                  key={stage.id}
                  stage={stage}
                  journeyMapType={journeyMapType}
                  currentView={currentView}
                  onUpdateStage={onUpdateStage}
                  onDeleteStage={onDeleteStage}
                  onAddTask={onAddTask}
                  onUpdateTask={onUpdateTask}
                  onDeleteTask={onDeleteTask}
                  onAddStep={onAddStep}
                  onUpdateStep={onUpdateStep}
                  onDeleteStep={onDeleteStep}
                  onSwitchToStepView={onSwitchToStepView}
                  onOpenStepDetail={onOpenStepDetail}
                />
              ))}
              
              {stages.length === 0 && (
                <div className="w-full text-center py-12 text-gray-500 bg-white min-h-full flex flex-col justify-center">
                  <svg className="w-16 h-16 mx-auto mb-4 text-gray-300" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={1} d="M19 11H5m14 0a2 2 0 012 2v6a2 2 0 01-2 2H5a2 2 0 01-2-2v-6a2 2 0 012-2m14 0V9a2 2 0 00-2-2M5 11V9a2 2 0 012-2m0 0V5a2 2 0 012-2h6a2 2 0 012 2v2M7 7h10" />
                  </svg>
                  <p className="text-lg font-medium">No {terminology.stages.toLowerCase()} yet</p>
                  <p className="text-sm">Add your first {terminology.stage.toLowerCase()} to get started</p>
                </div>
              )}
            </div>
          ) : (
            <div className="p-6 bg-white w-full h-full min-h-full">
              <AggregatedPainpointView 
                stages={stages}
                journeyMapType={journeyMapType}
                onSwitchToStepView={onSwitchToStepView}
                onOpenStepDetail={onOpenStepDetail}
                onUpdateStep={onUpdateStep}
                onDeleteStep={onDeleteStep}
                onDeleteTask={onDeleteTask}
                onDeleteStage={onDeleteStage}
                onOpenEditPanel={onOpenEditPanel}
                editPanel={editPanel}
                editablePainPoints={editablePainPoints}
                editableOpportunities={editableOpportunities}
                onCloseEditPanel={onCloseEditPanel}
                onAddPainPoint={onAddPainPoint}
                onRemovePainPoint={onRemovePainPoint}
                onUpdatePainPoint={onUpdatePainPoint}
                onAddOpportunity={onAddOpportunity}
                onRemoveOpportunity={onRemoveOpportunity}
                onUpdateOpportunity={onUpdateOpportunity}
                onSaveEditChanges={onSaveEditChanges}
              />
            </div>
          )}
        </div>
      </div>

      {/* Zoom and Pan Controls */}
      <ZoomPanControls
        zoom={zoom}
        zoomIn={zoomIn}
        zoomOut={zoomOut}
        resetZoom={resetZoom}
        fitToView={fitToView}
        canZoomIn={canZoomIn}
        canZoomOut={canZoomOut}
        isDragging={isDragging}
      />

      <AddStageModal
        isOpen={isAddStageModalOpen}
        onClose={() => setIsAddStageModalOpen(false)}
        onAdd={onAddStage}
        journeyMapType={journeyMapType}
      />

      <SpreadsheetImportExportModal
        isOpen={isImportExportModalOpen}
        onClose={() => setIsImportExportModalOpen(false)}
        stages={stages}
        journeyMapName={journeyMapName}
        journeyMapType={journeyMapType}
        onImportData={onImportData}
      />
    </div>
  );
};

export default JourneyMap; 